body, html, #root {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', sans-serif;
  height: 100%;
  background: linear-gradient(180deg, #6a11cb, #2575fc);
  color: white;
}

.app {
  text-align: center;
  padding: 20px;
}

.header {
  margin-bottom: 30px;
}

.profile-pic {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 50%;
  border: 3px solid white;
  margin-bottom: 15px;
}

h1 {
  font-size: 22px;
  margin: 0;
}

p {
  margin: 5px 0 20px;
  font-size: 16px;
  color: #e0e0e0;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}

.card {
  background-color: white;
  color: black;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.icon {
  font-size: 24px;
  margin-bottom: 10px;
}
.install-btn {
  margin-top: 30px;
  padding: 12px 24px;
  background-color: white;
  color: #6a11cb;
  font-weight: bold;
  border: none;
  border-radius: 20px;
  font-size: 16px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.15);
  cursor: pointer;
  transition: all 0.3s ease;
}

.install-btn:hover {
  background-color: #f0f0f0;
}

